"use client";

import { useParams, useRouter } from 'next/navigation';
import { useEffect, useState } from 'react';
import Chatbox from '../../components/chatbox'; // Ton composant Chatbox
import socket from '../../socket';

const QuizRoom = () => {
    const router = useRouter();
    const { roomId } = useParams();  // Récupère le roomId de l'URL
    const [username, setUsername] = useState('');
    const [players, setPlayers] = useState([]);
    const [answer, setAnswer] = useState('');

    useEffect(() => {
        if (roomId) {
            const storedUsername = localStorage.getItem('username');
            setUsername(storedUsername || '');  // Optionnel: récupérer le nom d'utilisateur stocké localement

            socket.emit('joinRoom', { roomId, username });
        }
    }, [roomId, username]);

    useEffect(() => {
        socket.on('roomUpdate', (room) => {
            setPlayers(room.players);
        });

        return () => {
            socket.off('roomUpdate');
        };
    }, []);

    const sendAnswer = () => {
        socket.emit('chat message', { roomId, message: answer });
        setAnswer('');  // Vider le champ après envoi
    };

    return (
        <div className="relative">
            <h1>Room: {roomId}</h1>
            <div>
                <h2>Joueurs dans la room</h2>
                <ul>
                    {players.map(player => (
                        <li key={player.id}>{player.username}</li>
                    ))}
                </ul>
            </div>

            <div>
                <h2>Question : {question}</h2>
                <input type="text" placeholder="Votre réponse" value={answer} onChange={(e) => setAnswer(e.target.value)} />
                <button onClick={sendAnswer}>Envoyer la réponse</button>
            </div>

            <Chatbox /> 
        </div>
    );
};

export default QuizRoom;
